var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("X战警")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        X战警是一个团队虚构的\n           超级英雄出现在美国漫画书的出版奇迹漫画。由艺术家/合作作家杰克柯比和作家斯坦李创作，角色首次出现在\n           X战警＃1（1963年9月）。它们是漫威漫画最知名和最成功的知识产权之一，出现在众多书籍，电视节目，电影和\n           视频游戏中。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("    \n               大多数X战警是突变体，是由“X-Gene”激活的具有超人能力的人类的亚种。在一个叛逆\n           偏见激烈和普遍存在的世界里，X战警为正常人类和突变体之间的和平与平等而战。他们由Charles Xavier领导，也\n           被称为X教授，他是一个强大的突变心灵感应者，可以控制和阅读思想。他们的大敌是Magneto，一个强大的突变体，具\n           有操纵和控制磁场的能力，是突变体兄弟会的领导者。关于突变体和人类之间的关系，两者都有相反的观点和哲学。虽然\n           前者致力于突变体与人类之间的和平与理解，但后者认为人类是一种威胁，并且相信采取积极的态度对付他们，尽管他\n           发现自己不时与X战警并肩作战。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("     \n              X教授是Xavier的天才青少年学校的创始人，该学校位于通常被称为X-Mansion的地方，\n           从世界各地招募突变体。位于塞勒姆中心在威彻斯特县，纽约，X豪宅是X战警的家庭和训练场地。\n           在X战警＃1（1963年9月）中出现的X战警的五名成员是天使（天使长），野兽，独眼巨人，冰人和奇迹女郎（让·格雷）\n           ; X教授和Magneto 教授也首次出现在The X-Men中＃1。\n           从那以后，来自不同国家和不同背景的数十种突变体，甚至一些非突变体，都成为X战警的成员。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        1963年，随着蜘蛛侠在神奇幻想中的成功，以及绿巨人，雷神，钢铁侠和神奇四侠，共同创造者斯坦李想要创造另一组超级英雄，但不想解释他们是如何获得权力的。2004年，李回忆说：“我不能让所有人被放射性蜘蛛咬伤或暴露于伽马射线爆炸。我采取了懦弱的方式。我对自己说，'为什么我不说他们是突变体。他们天生就是这样。' “ 在1987年的采访中，柯比说，X战警，我在那里做了自然的事情。对于只是普通男孩和女孩的突变体你会做什么，当然不是危险的？你上学了。你发展他们的技能。因此，我给了他们一位老师，X教授。当然，这是很自然的事情，而不是迷惑或疏远与我们不同的人，我让X战警成为人类的一部分，他们就是这样。可能的是，辐射如果有益，可能会产生突变体，这些突变体会拯救我们而不是伤害我们。我觉得如果我们按照自己的方式训练突变体，他们会帮助我们 - 不仅帮助我们，而且实现自己意义上的增长。所以，我们都可以共同生活。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在Marvel出版商马丁古德曼拒绝了最初的名字“The Mutants” 之后，Lee设计了系列片头，并指出读者不会知道什么是“突变体”。在漫威宇宙中，X战警被广泛认为是以泽维尔教授本人的名字命名的。Xavier在X战警＃1（1963）中提供的这个名称的原始解释是，突变体“具有额外的力量......普通人不具备的力量!!这就是我称呼我的学生的原因...... X战警，对于EX-tra动力！“ ")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
